/* General styles */
.home-container {
  background-color: hsl(0, 0%, 22%);
  background-size: cover;
}

header {
  display: flex;
  height: auto;
  justify-content: space-around;
  align-items: center;
  z-index: 10;
  
}
header .navbar1{
  display: none;
}
header .logo{
  display: flex;
  justify-content: center;
  align-items: center;
}
header .logo  .logo-text h1{
     font-size: 25px;
     color: aliceblue;
}
header .logo  .logo-text p{
  font-size: 13px;
  color: white;
}

header .logo img {
  width: 80px;
  object-fit: cover;
}

header .location,
header .phone {
  display: flex;
  justify-content: center;
  align-items: center;
}

header .location p,
header .phone p {
  color: rgb(250, 250, 250);
  text-align: center;
}

header .contact-icon img {
  width: 35px;
  padding: 15px 16px;
  margin: 0 10px;
}

header .location a,
header .phone a {
  color: rgb(212, 212, 212);
  text-decoration: none;
}

@media (max-width: 767px) {
  header .logo-text{
    display: none;
  }
  header {
    padding:5px;
    z-index: 999;
    top: 0px;
    position: sticky;
    backdrop-filter: blur(15px);
  }
  header .navbar1{
    display: block;  
    border-radius: 16px;
    margin-left: 150px;
  }
  .yt,
  header .location,
  header .phone {
    display: none;
  }

  header .logo img {
    width: 80px;
    height: 80px;
    object-fit: cover;
  }

  header p {
    font-size: 14px;
  }
}
@keyframes slide-left {
   
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(0);
  }
  
}

.navbar {
  background: rgba(52, 191, 255, 0.6);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  color: rgb(25, 25, 25);
  z-index: 999;
  width: 80%;
  top: 0px;
  position: sticky;
  max-width: 100%;
  margin: 0 auto;
  margin-bottom: 10px;
}

nav {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

nav a {
  margin: 0 25px;
  text-decoration: none;
  font-size: 17px;
  border-radius: 5px;
  padding: 3px 10px;
  color: rgb(61, 61, 61);
}

nav a:hover {
  background-color: aliceblue;
  color: #333;
}

nav ul {
  list-style: none;
}

nav ul ul {
  display: none;
}

nav ul li:hover {
  display: block;
}

.responsive {
  display: none;
}

.menu-icon {
  cursor: pointer;
  padding: 3px 7px;
  box-shadow: 0 0 5px 0 grey;
  backdrop-filter: blur(3px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.menu-icon .bar {
  width: 30px;
  height: 3px;
  background-color: white;
  margin: 6px 0;
  transition: 0.4s;
  z-index: 10;
}
.menu-icon .bar1 {
  width: 50px;
  height: 3px;
  background-color: white;
  margin: 6px 0;
  transition: 0.4s;
  z-index: 10;
}

.show {
  display: flex;
  flex-direction: column;
  background-color: #1e1e1e;
  z-index: 10;
}

.menu-icon.close div:nth-child(1) {
  transform: rotate(-45deg) translate(-14px, 6px);
}

.menu-icon.close div:nth-child(2) {
  opacity: 0;
}

.menu-icon.close div:nth-child(3) {
  transform: rotate(45deg) translate(-14px, -6px);
}

.show-navitems {
  margin: 0 30px;
  text-decoration: none;
  color: white;
  font-size: 17px;
  border-radius: 5px;
  padding: 10px 20px;
  animation: mymove 1s;
}

@media (max-width: 800px) {

  .menu-icon {
    cursor: pointer;
    padding: 2px 6px;
    box-shadow: none;
    backdrop-filter: blur(3px);
  }
  .menu-icon div {
    width: 27px;
    height: 2.5px;
    background-color: rgb(255, 255, 255);
    margin: 6px 0;
    transition: 0.4s;
    z-index: 10;
  }

  nav {
    display: none;
  }

  .responsive {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
  }
  .navbar{
    display: none;
  }

  .responsive .nav-logo img {
    width: 80px;
    border-radius: 100%;
    margin-left: 20px;
  }

  .responsive .menu-icon {
    margin-right: 20px;
  }

  .show {
    display: flex;
    flex-direction: column;
    align-items: start;
    height: 90vh;
    background-color: #1e1e1e;
    width: 100%;
    position: absolute;
    top: 90px;
    left: 0;
    animation: slide-left 1s;
  }
  .show-navitems {
    margin: 0 10px;
    text-decoration: none;
    color: white;
    font-size: 26px;
    border-radius: 5px;
    padding: 20px 20px;
  }
}

/*Slide Show */
.slideshow {
  position: relative;
  width: 100%;
  overflow: hidden;
  
  background-color: white;
}

.slideshow-images img {
  width: 100%;
  object-fit: fill;
  margin-bottom: 60px;
}

button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  border: none;
  background-color: rgba(0, 0, 0, 0.0);
  padding: 10px;
  cursor: pointer;
}

button:hover {
  background-color: rgba(0, 0, 0, 0.3);
  border-radius: 10px;
}

button:first-of-type {
  left: 10px;
}

button:last-of-type {
  right: 10px;
}
.whatsapp-button {
  position: fixed;
  bottom: 35px;
  right: 35px;
  border-radius: 50%;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  z-index: 1000;
  text-decoration: none;
}

.whatsapp-button img {
  width: 60px;
  height: 60px;
}
@media(max-width:789px){
  .whatsapp-button img {
    width: 50px;
    height: 50px;
  }
  .slideshow {
    position: relative;
    width: 100%;
    overflow: hidden;
    height: 200px;
    background-color: white;
  }
  
  .slideshow-images img {
    width: 100%;
    height: 200px;
    object-fit: fill;
    margin-bottom: 60px;
  }
  
}
